/* button */
.filled-btn {
  display: inline-block;
  width: 200px;
  padding: 15px 24px;
  border-radius: 4px;
  font-size: 16px;
  font-weight: bold;
  line-height: normal;
  text-align: center;
  color: #fff;
  background-color: #0f42cf;
  vertical-align: middle;
  font-family: SamsungOne;
}
.lined-btn {
  display: inline-block;
  width: 200px;
  padding: 15px 24px;
  border-radius: 4px;
  border: 1px solid #d6d6d6;
  font-size: 16px;
  font-weight: bold;
  line-height: normal;
  text-align: center;
  color: #363636;
  background-color: #fff;
  vertical-align: middle;
  font-family: SamsungOne;
}
.filled-btn:hover:not([disabled]) {
  background-color: #002ba3;
}
.lined-btn:hover:not([disabled]) {
  background-color: #d6d6d6;
}
/* /button */
/* checkbox */
.checkbox-wrap.error + .error-msg {
  display: block;
}
.checkbox-wrap + .error-msg {
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: #ec2000;
  display: none;
  margin: 8px 0 0 0;
}
.check-wrap {
  display: block;
  padding: 9px 40px;
  background: #fff;
  position: relative;
}
.check-wrap input[type="checkbox"] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.check-wrap input[type="checkbox"] + label span {
  position: relative;
  display: inline-block;
  padding: 2px 12px 0 26px;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: -0.11px;
  color: #363636;
}
.check-wrap input[type="checkbox"] + label span:before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  background: url(/images/icon_checkbox.png) no-repeat center / cover;
}
.check-wrap.required input[type="checkbox"] + label span:after {
  content: "*";
  position: absolute;
  right: 0;
  top: 0;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: -0.11px;
  color: #ec2000;
}
.check-wrap input[type="checkbox"]:checked + label span:before {
  background-image: url(/images/icon_checkbox_on.png) !important;
}
.check-wrap.required input[type="checkbox"]:checked + label span:before {
  opacity: 0.4;
}
.check-wrap.required input[type="checkbox"]:checked + label {
  pointer-events: none;
}
.check-wrap.error input[type="checkbox"] + label span:before {
  background-image: url(/images/icon_checkbox_error.png);
}

.check-wrap .handle {
  position: absolute;
  right: 40px;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  display: none;
}
.check-wrap .handle img {
  width: 100%;
}
.ui-sortable-helper .check-wrap {
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2);
}
@media (hover: hover) {
  .check-wrap:hover .handle {
    display: block;
  }
}
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .check-wrap:hover .handle {
    display: block;
  }
}
/* /checkbox */
/* radio */
.excel-radio + .excel-radio {
  margin-top: 10px;
}
.excel-radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.excel-radio label .text {
  position: relative;
  display: inline-block;
  padding-left: 24px;
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: -0.16px;
  color: #363636;
  cursor: pointer;
  text-align: left;
}
.excel-radio label .text:before {
  content: "";
  position: absolute;
  left: 0;
  top: 2px;
  width: 16px;
  height: 16px;
  background: url(/images/radio-off.png) no-repeat center / cover;
}
.excel-radio input:checked + label .text:before {
  background-image: url(/images/radio-on.png);
}
/* /radio */

.right-wrap {
  margin-bottom: 40px;
