}
.input-text.error {
  border-color: #ec2000;
  padding-right: 36px;
  background: url(/images/error.png) no-repeat #fff calc(100% - 9px) center /
    16px;
}
.input-text.error + .error-msg {
  display: block;
}
.input-text.error {
  border-color: #ec2000;
  padding-right: 36px;
  background: url(/images/error.png) no-repeat #fff calc(100% - 9px) center /
    16px;
}
.input-text.big.error {
  padding-right: 48px;
