.pop-wrap {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  display: none;
  z-index: 31;
}
.pop-wrap::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.3;
}
.pop-wrap .tit_box {
  position: relative;
  padding: 0 40px;
}
.pop-wrap .tit_box .close {
  position: absolute;
  right: 40px;
  top: 50%;
  transform: translateY(-50%);
}
.pop-wrap .tit_box .close img {
  width: 24px;
}
.pop-wrap .tit_box h3 {
  font-size: 24px;
  font-weight: bold;
  line-height: 1.33;
  letter-spacing: -0.24px;
  color: #000000;
}
.pop-wrap .scrollHeight {
  margin-top: 40px;
  height: 676px;
  max-height: calc(100vh - 330px);
  overflow-y: auto;
  padding: 40px 40px 0;
  border-top: 1px solid #ebebed;
  border-bottom: 1px solid #ebebed;
}
.pop-wrap .scrollHeight:after {
  content: "";
  display: block;
  padding-bottom: 40px;
}
.pop-wrap .scrollHeight::-webkit-scrollbar {
  width: 4px;
}
.pop-wrap .scrollHeight::-webkit-scrollbar-track {
  background: #fff;
}
.pop-wrap .scrollHeight::-webkit-scrollbar-thumb {
  background-color: #d7d7d7;
  border-radius: 2px;
}
.pop-wrap .scrollHeight li strong {
  font-size: 16px;
  font-weight: bold;
  line-height: 1.5;
  letter-spacing: -0.16px;
  color: #6e6e6e;
}
.pop-wrap .scrollHeight li strong.star {
  position: relative;
  padding-right: 10px;
}
.pop-wrap .scrollHeight li strong.star::after {
  content: "*";
  font-size: 14px;
  font-weight: bold;
  line-height: 1.14;
  letter-spacing: -0.1px;
  color: #ec2000;
  position: absolute;
  right: 0;
  top: -2px;
}
.pop-wrap .scrollHeight li div {
  margin-top: 8px;
}
.pop-wrap .scrollHeight li select {
  width: 100%;
  height: 48px;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
  background-color: #ffffff;
  box-sizing: border-box;
  appearance: none;
