}
.comment-head .issue-info {
  margin-right: 64px;
}
.comment-head .issue-info .no {
  display: inline-block;
  margin-right: 8px;
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 1;
  letter-spacing: -0.11px;
  color: #6e6e6e;
