  letter-spacing: -0.14px;
  color: #9e9e9e;
}
.comment-wrap .comment.active .count .cur {
  color: #363636;
}

.table-filter {
  position: relative;
  margin-top: 40px;
  z-index: 20;
}
.filter-item {
  position: relative;
  width: 200px;
  display: inline-block;
  vertical-align: top;
}
.filter-item.short {
  width: 160px;
}
.filter-item.long {
  width: 260px;
}
.filter-item + .filter-item {
  margin-left: 8px;
}
.filter-item .title {
  display: block;
  margin-bottom: 6px;
  font-size: 14px;
  font-weight: bold;
  line-height: 1.43;
  letter-spacing: -0.14px;
  color: #6e6e6e;
}
.filter-item .input-text.active + .dropdown-wrap {
  display: block;
}
.filter-item .selected-text {
  position: relative;
  display: block;
  padding: 5px 36px 5px 12px;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
  font-size: 14px;
  line-height: 1.43;
  color: #363636;
}
.filter-item .selected-text:before {
  content: "";
  position: absolute;
  top: 50%;
  right: 12px;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  background: url(/images/arrow-light.png) no-repeat center / cover;
}
.filter-item .selected-text.active {
  border-color: #0f42cf;
}
.filter-item .selected-text.active:before {
  transform: translateY(-50%) rotate(180deg);
}
.filter-item .selected-text span {
  display: block;
  max-width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.filter-item .selected-text.active + .dropdown-wrap {
  display: block;
}
.dropdown-wrap {
  display: none;
  position: absolute;
  top: calc(100% + 4px);
  left: 0;
  right: 0;
  background-color: #fff;
  z-index: 20;
  padding: 8px 0;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
  max-height: 378px;
  overflow: auto;
}
.dropdown-wrap ul {
  display: block;
  margin: 0;
}
.dropdown-wrap ul li {
  flex: none;
  width: 100%;
  padding: 0;
}
.dropdown-wrap ul li a {
  display: block;
  padding: 10px 12px;
  color: #363636;
  font-size: 14px;
  line-height: 1.43;
}
.dropdown-wrap ul li button {
  display: block;
  padding: 10px 12px;
  color: #363636;
  font-size: 14px;
  line-height: 1.43;
  width: 100%;
  text-align: left;
  font-family: SamsungOne;
}
.dropdown-wrap ul li.recent a {
  padding-left: 40px;
  background: url(/images/icon_time.png) 16px center no-repeat;
  background-size: 16px;
}
.dropdown-wrap ul li:hover a {
  background-color: rgba(15, 66, 207, 0.1);
}
.dropdown-wrap ul li:hover button {
  background-color: rgba(15, 66, 207, 0.1);
}

@media all and (max-width: 1000px) {
  .comment-wrap {
    margin-top: 0;
  }
