    height: 42px;
  }
  .comment-head .issue-info .input-text input[type="text"] {
    height: 40px;
  }
  .input-file input[type="file"] + label {
    width: 40px;
    height: 40px;
    background-size: 24px;
  }
  .comment-bottom .apply-btn {
    padding: 10px 24px;
    font-size: 16px;
  }

  .filter-item {
    display: block;
    width: 100%;
  }
  .filter-item.short {
    width: 100%;
  }
  .filter-item.long {
    width: 100%;
  }
  .filter-item + .filter-item {
    margin-left: 0;
    margin-top: 16px;
  }
  .input-text {
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-text input[type="text"] {
    height: 40px;
  }
  .input-text.active {
    padding-right: 36px;
  }
  .input-text.search {
    padding-left: 36px;
    background-position: 12px center;
  }
  .input-text input[type="text"] + button {
    right: 12px;
  }
  .filter-item .selected-text {
    padding-top: 9px;
    padding-bottom: 9px;
    font-size: 16px;
  }
}
/* datetimepicker */
