<script>
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";

export default {
  components: { DatePicker },
  data() {
    return {
      time1: null,
      format: "YY-MM-DD",
      lang: {
        formatLocale: {
          weekdaysMin: ["S ", "M", "T ", "W", "T", "F", "S"],
        },
      },
    };
  },
  methods: {
    selectPrev7Day(emit) {
      const start = new Date();
      const end = new Date();
      start.setTime(end.getTime() - 7 * 24 * 3600 * 1000);
      const date = [start, end];
      emit(date);
    },
    selectPrev30Day(emit) {
      const start = new Date();
      const end = new Date();
      start.setTime(end.getTime() - 30 * 24 * 3600 * 1000);
      const date = [start, end];
      emit(date);
    },
    selectPrev60Day(emit) {
      const start = new Date();
      const end = new Date();
      start.setTime(end.getTime() - 60 * 24 * 3600 * 1000);
      const date = [start, end];
      emit(date);
    },
    selectPrev90Day(emit) {
      const start = new Date();
      const end = new Date();
      start.setTime(end.getTime() - 90 * 24 * 3600 * 1000);
      const date = [start, end];
      emit(date);
    },
    toggleFooter(value, type) {
      if (type === "date") {
        const footer = document.getElementsByClassName(
          "mx-datepicker-footer"
        )[0];
        footer.classList.add("on");
      }
    },
    open() {
      const filterBox = document.getElementsByClassName("mx-datepicker")[0];
      filterBox.classList.add("active");
    },
    close() {
      const filterBox = document.getElementsByClassName("mx-datepicker")[0];
      filterBox.classList.remove("active");
    },
    handleApply() {
      this.$refs.datepicker.closePopup();
    },
  },
};
</script>
