  border-radius: 4px;
  border: solid 1px #d6d6d6;
  background-color: #fff;
}
.input-text.big {
  padding: 0 12px;
}
.input-text.search {
  padding-left: 32px;
  background: url(/images/input-search.png) no-repeat #fff 8px center / 16px
    17px;
}
.input-text input[type="text"] {
  width: 100%;
  height: 30px;
  line-height: 20px;
  font-family: SamsungOne, "Apple SD Gothic Neo", "Apple SD  Neo",
    AppleGothic, arial, sans-serif;
  font-size: 14px;
  color: #363636;
}
.input-text input[type="text"] + button {
  display: none;
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  padding: 3px;
  line-height: 0;
  font-size: 0;
}
.input-text input[type="text"] + button img {
  max-width: 100%;
}
.input-text.active input[type="text"] + button {
  display: block;
}
.input-text.active.error input[type="text"] + button {
  display: none;
}
.input-text.active {
  padding-right: 32px;
}
.input-text.big input[type="text"] {
  height: 46px;
  font-size: 16px;
  line-height: 1.38;
}
.input-text.active {
  border-color: #0f42cf;
}
.input-text.error {
  border-color: #ec2000;
  padding-right: 36px;
  background: url(/images/error.png) no-repeat #fff calc(100% - 9px) center /
    16px;
}
.input-text.big.error {
  padding-right: 48px;
  background-size: 24px;
}
.input-file {
  position: relative;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
}
.input-file input[type="file"] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.input-file input[type="file"] + label {
  display: block;
  width: 32px;
  height: 32px;
  font-size: 0;
  line-height: 0;
  background: url(/images/attachment.png) no-repeat center transparent;
  background-size: 16px;
  cursor: pointer;
}
.textarea {
  width: 100%;
  height: 100px;
  font-size: 14px;
  line-height: 1.43;
  letter-spacing: -0.14px;
  color: #363636;
  resize: none;
  scrollbar-width: none;
  -ms-overflow-style: none;
  overflow-x: scroll;
}
.textarea::-webkit-scrollbar {
  display: none;
}
/* /input */
.comment-wrap {
  position: relative;
  padding: 24px 16px 16px;
  border-radius: 4px;
  background-color: #f7f7f7;
}
.comment-wrap .comment-head {
  position: relative;
}
.comment-head .issue-info {
  margin-right: 64px;
}
.comment-head .issue-info .no {
  display: inline-block;
  margin-right: 8px;
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 1;
  letter-spacing: -0.11px;
  color: #6e6e6e;
  vertical-align: middle;
}
.comment-head .issue-info .input-text {
  display: inline-block;
  width: 200px;
  vertical-align: middle;
  margin-bottom: 8px;
}
.comment-head .input-file {
  position: absolute;
  top: 0;
  right: 0;
}
.comment-wrap .comment {
  position: relative;
  padding: 16px 16px 36px;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
  background-color: #fff;
}
.comment-bottom {
  margin-top: 8px;
  font-size: 0;
  line-height: 0;
}
.comment.error {
  border-color: #ec2000;
}
.comment-wrap .error-msg {
  font-size: 14px;
  line-height: 1.43;
  letter-spacing: -0.14px;
  color: #ec2000;
  display: none;
  padding-left: 8px;
  margin-top: 8px;
}
.comment.error + .error-msg {
  display: block;
}
.comment-bottom:after {
  content: "";
  display: block;
  clear: both;
}
.comment-bottom .attached {
  position: relative;
  display: inline-block;
  padding: 13px 48px 13px 8px;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
}
.comment-bottom .attached .name {
  display: inline-block;
  padding-left: 24px;
  font-size: 14px;
  line-height: 1.43;
  letter-spacing: -0.14px;
  color: #6e6e6e;
  background: url(/images/file.png) no-repeat left center;
  background-size: 16px;
}
.comment-bottom .attached .delete-btn {
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  background: url(/images/close-light.png) no-repeat center / cover;
}
.comment-bottom .apply-btn {
  float: right;
  width: 120px;
  margin-top: 8px;
  padding: 7px 24px;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
  font-size: 14px;
  font-weight: bold;
  line-height: normal;
  color: #363636;
  font-family: SamsungOne;
}
.comment-bottom .apply-btn:hover:not([disabled]) {
  background-color: #d6d6d6;
}
.comment-wrap .comment .count {
  position: absolute;
  bottom: 8px;
  right: 12px;
  font-size: 14px;
  line-height: 1.43;
  letter-spacing: -0.14px;
  color: #9e9e9e;
}
.comment-wrap .comment.active .count .cur {
  color: #363636;
}

.table-filter {
  position: relative;
  margin-top: -22px;
}
.filter-item {
  position: relative;
  width: 200px;
  display: inline-block;
  vertical-align: top;
}
.filter-item.short {
  width: 160px;
}
.filter-item + .filter-item {
  margin-left: 8px;
}
.filter-item .title {
  display: block;
  margin-bottom: 6px;
  font-size: 14px;
  font-weight: bold;
  line-height: 1.43;
  letter-spacing: -0.14px;
  color: #6e6e6e;
}
.filter-item .input-text.active + .dropdown-wrap {
  display: block;
}
.filter-item .selected-text {
  position: relative;
  display: block;
  padding: 5px 36px 5px 12px;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
  font-size: 14px;
  line-height: 1.43;
  color: #363636;
}
.filter-item .selected-text:before {
  content: "";
  position: absolute;
  top: 50%;
  right: 12px;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  background: url(/images/arrow-light.png) no-repeat center / cover;
}
.filter-item .selected-text.active {
  border-color: #0f42cf;
}
.filter-item .selected-text.active:before {
  transform: translateY(-50%) rotate(180deg);
}
.filter-item .selected-text span {
  display: block;
  max-width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.filter-item .selected-text.active + .dropdown-wrap {
  display: block;
}
.dropdown-wrap {
  display: none;
  position: absolute;
  top: calc(100% + 4px);
  left: 0;
  right: 0;
  background-color: #fff;
  z-index: 20;
  padding: 8px 0;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
  max-height: 378px;
  overflow: auto;
}
.dropdown-wrap ul {
  display: block;
  margin: 0;
}
.dropdown-wrap ul li {
  flex: none;
  width: 100%;
  padding: 0;
}
.dropdown-wrap ul li a {
  display: block;
  padding: 10px 12px;
  color: #363636;
  font-size: 14px;
  line-height: 1.43;
}
.dropdown-wrap ul li button {
  display: block;
  padding: 10px 12px;
  color: #363636;
  font-size: 14px;
  line-height: 1.43;
  width: 100%;
  text-align: left;
  font-family: SamsungOne;
}
.dropdown-wrap ul li.recent a {
  padding-left: 40px;
  background: url(/images/icon_time.png) 16px center no-repeat;
  background-size: 16px;
}
.dropdown-wrap ul li:hover a {
  background-color: rgba(15, 66, 207, 0.1);
}
.dropdown-wrap ul li:hover button {
  background-color: rgba(15, 66, 207, 0.1);
}

@media all and (max-width: 1000px) {
  .comment-bottom .attached {
    display: block;
  }
  .comment-bottom .attached + .apply-btn {
    margin-top: 16px;
  }
  .comment-head .issue-info .input-text {
    width: 160px;
    padding-top: 0;
    padding-bottom: 0;
    height: 42px;
  }
  .comment-head .issue-info .input-text input[type="text"] {
    height: 40px;
  }
  .input-file input[type="file"] + label {
    width: 40px;
    height: 40px;
    background-size: 24px;
  }
  .comment-bottom .apply-btn {
    padding: 10px 24px;
    font-size: 16px;
  }

  .filter-item {
    display: block;
    width: 100%;
  }
  .filter-item.short {
    width: 100%;
  }
  .filter-item + .filter-item {
    margin-left: 0;
    margin-top: 16px;
  }
  .input-text {
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-text input[type="text"] {
    height: 40px;
  }
  .input-text.active {
    padding-right: 36px;
  }
  .input-text.search {
    padding-left: 36px;
    background-position: 12px center;
  }
  .input-text input[type="text"] + button {
    right: 12px;
  }
  .filter-item .selected-text {
    padding-top: 9px;
    padding-bottom: 9px;
    font-size: 16px;
  }
}
/* datetimepicker */
.mx-datepicker-range {
  width: 100%;
}
.mx-datepicker >>> .mx-input {
  max-width: 100%;
  height: 32px;
  padding: 6px 56px 6px 12px;
  border-color: #d6d6d6;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 14px;
  line-height: 1.43;
  letter-spacing: normal;
  color: #000;
  font-family: SamsungOne;
  background: #ffffff url(/images/ic-calendar.png) calc(100% - 12px) center
    no-repeat;
  background-size: 16px;
  box-shadow: none;
}
.mx-datepicker >>> .mx-input:hover {
  border-color: #d6d6d6;
}
.mx-datepicker >>> .mx-input:focus {
  border-color: #0f42cf;
}
.mx-datepicker >>> .mx-input::placeholder {
  font-weight: normal !important;
}
.mx-datepicker >>> .mx-icon-calendar {
  display: none;
}
.picker-wrap >>> .mx-icon-clear {
  right: 36px;
  font-size: 0;
}
.clear-icon {
  display: block;
  width: 16px;
  height: 16px;
}
.mx-datepicker >>> .calendar-icon {
  display: block;
  width: 24px;
  height: 24px;
}
.mx-datepicker >>> .calendar-icon img,
.clear-icon img {
  width: 100%;
}
.mx-datepicker.active .calendar-icon {
  opacity: 1;
}
.mx-icon-left:before,
.mx-icon-right:before,
.mx-icon-double-left:before,
.mx-icon-double-right:before,
.mx-icon-double-left:after,
.mx-icon-double-right:after {
  width: 14px;
  height: 14px;
  margin-left: -4px;
  border-width: 3px 0 0 3px;
  border-color: #000;
}
.mx-calendar-panel-month .mx-icon-left:after,
.mx-calendar-panel-month .mx-icon-right:after,
.mx-calendar-panel-month .mx-icon-double-left:after,
.mx-calendar-panel-month .mx-icon-double-right:after {
  display: none;
}
.mx-calendar-panel-year .mx-icon-left:after,
.mx-calendar-panel-year .mx-icon-right:after,
.mx-calendar-panel-year .mx-icon-double-left:after,
.mx-calendar-panel-year .mx-icon-double-right:after {
  display: none;
}
.picker-wrap >>> .mx-input-wrapper .mx-icon-clear {
  display: block;
}

/* calendar */
.mx-datepicker-main {
  border-radius: 4px;
}
.mx-datepicker-header {
  padding: 12px 0;
  border-color: #ebebed;
}
.mx-datepicker-header .mx-btn {
  margin: 0 7px;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 12px;
  line-height: 1.67;
  letter-spacing: -0.12px;
  color: rgba(54, 54, 54, 0.8);
  font-family: SamsungOne, "Apple SD Gothic Neo", "Apple SD  Neo",
    AppleGothic, arial, sans-serif;
}
.mx-datepicker-header .mx-btn:first-child {
  margin-left: 5px;
}
.mx-datepicker-header .mx-btn:hover {
  background-color: rgba(15, 66, 207, 0.1);
}
.mx-datepicker-footer {
  display: none;
  padding: 12px 16px;
}
.mx-datepicker-footer.on {
  display: block;
}
.mx-datepicker-footer .mx-btn {
  padding: 5px 16px 4px;
  border-radius: 4px;
  border: solid 1px #0f42cf;
  color: #0f42cf;
  font-weight: bold;
  font-size: 12px;
  font-family: SamsungOne, "Apple SD Gothic Neo", "Apple SD  Neo",
    AppleGothic, arial, sans-serif;
  line-height: normal;
}
.mx-calendar {
  padding: 0 16px 32px;
}

/* header, footer */
.mx-calendar-header,
.mx-time-header {
  margin-top: 16px;
  height: 36px;
  line-height: 36px;
}
.mx-calendar-header-label .mx-btn,
.mx-time-header .mx-btn,
.mx-calendar-header-label {
  font-size: 16px;
  font-family: SamsungOne, "Apple SD Gothic Neo", "Apple SD  Neo",
    AppleGothic, arial, sans-serif;
  font-weight: bold;
  color: #363636;
}
.mx-table th {
  font-size: 12px;
  font-weight: normal;
  color: #d6d6d6;
}
.mx-table-date td {
  color: #363636;
  font-family: SamsungOne, "Apple SD Gothic Neo", "Apple SD  Neo",
    AppleGothic, arial, sans-serif;
}
.mx-table-date .cell.not-current-month {
  color: #d6d6d6;
}
.mx-table-date .today {
  color: #0f42cf;
}
.mx-calendar-content .cell.active {
  border-radius: 4px;
  background-color: #0f42cf;
}
.mx-calendar-content .cell.in-range,
.mx-calendar-content .cell.hover-in-range,
.mx-calendar-content .cell:not(.active):hover {
  background-color: rgba(15, 66, 207, 0.1);
}

/* time */
.mx-time-content {
  padding: 0 26px;
}
.mx-time-header {
  border: none;
}
.mx-time-column {
  margin: 0 5px;
  border: none;
}
.mx-time-column .mx-time-item {
  height: 24px;
  line-height: 24px;
  border-radius: 4px;
  color: #363636;
  font-family: SamsungOne, "Apple SD Gothic Neo", "Apple SD  Neo",
    AppleGothic, arial, sans-serif;
}
.mx-time-column .mx-time-item:hover {
  background-color: rgba(15, 66, 207, 0.1);
}
.mx-time-column .mx-time-item.active {
  color: #fff;
  font-weight: normal;
  background-color: #0f42cf;
}
.mx-time-column .mx-time-list::after {
  display: none;
}

/* month, year */
.mx-calendar-panel-month,
.mx-calendar-panel-year {
  padding: 0 36px;
}
.mx-calendar-panel-month .mx-calendar-header,
.mx-time-header,
.mx-calendar-panel-year .mx-calendar-header,
.mx-time-header {
  margin: 16px -20px 0;
}
.mx-table.mx-table-month {
  height: 180px;
}
.mx-table-month .cell,
.mx-table-year .cell {
  padding: 0 4px;
  color: #363636;
  font-family: SamsungOne, "Apple SD Gothic Neo", "Apple SD  Neo",
    AppleGothic, arial, sans-serif;
}
.mx-table-month .cell div,
.mx-table-year .cell div {
  padding: 5px;
  border-radius: 4px;
}
.mx-table-month .cell.active,
.mx-table-year .cell.active {
  background-color: transparent;
}
.mx-table-month .cell.active div,
.mx-table-year .cell.active div {
  padding: 5px 0;
  border-radius: 4px;
  background-color: #0f42cf;
}
.mx-table-month .cell:not(.active):hover,
.mx-table-year .cell:not(.active):hover {
  background-color: transparent;
  color: #363636;
}
.mx-table-month .cell:not(.active):hover div,
.mx-table-year .cell:not(.active):hover div {
  background-color: rgba(15, 66, 207, 0.1);
}
/* /datetimepicker */
@media all and (max-width: 1000px) {
  .input-text-wrap .text {
    display: block;
    margin-right: 0;
    margin-bottom: 8px;
    padding-right: 0;
  }
  .input-text-wrap .text:before {
    display: none;
  }
  .input-text-wrap .picker-wrap {
    width: 100%;
  }
  .mx-calendar,
  .mx-time {
    width: 100%;
  }
  .mx-datepicker-popup {
    max-height: 80vh;
    overflow-x: auto;
  }
  .input-text-wrap .select-wrap {
    width: 100%;
  }

  .mx-datepicker >>> .mx-input {
    height: 42px;
    padding: 6px 84px 6px 16px;
    background-position: calc(100% - 16px) center;
    background-size: 24px;
  }
  .picker-wrap >>> .mx-icon-clear {
    right: 52px;
  }
  .clear-icon {
    width: 24px;
    height: 24px;
  }
}

