  width: 120px;
  height: 32px;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
  font-family: SamsungOne;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #363636;
}
.tableWrap .titArea .alignRight .del {
  margin-left: 8px;
  width: 96px;
  height: 32px;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
  font-family: SamsungOne;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #363636;
}
.tableWrap .titArea .alignRight .edit {
  margin-left: 8px;
  width: 96px;
  height: 32px;
  border-radius: 4px;
  border: solid 1px #d6d6d6;
  font-family: SamsungOne;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #363636;
}
.tableWrap .titArea .alignRight .add {
  margin-left: 8px;
