}
.tableWrap.titOn {
}
.tableWrap.titOn .titArea {
  z-index: 20;
}
.tableWrap.titOn .tableSt01 {
  z-index: 10;
}
.tableSt01 {
}
.tableSt01.headOn > table {
  z-index: 10;
}
.tableSt01.headOn > .scroll {
  position: relative;
  z-index: 9;
}
.tableSt01 table {
  table-layout: fixed;
  position: relative;
}
.tableSt01 thead th {
  position: relative;
  background: #f7f7f7;
  border-bottom: 1px solid #ebebed;
  text-align: left;
}
.tableSt01 thead th .inbox {
  position: relative;
  padding: 22px 32px 18px 16px;
}
.tableSt01 thead th .inbox::after {
  content: "";
  width: 1px;
  height: 32px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background: #ebebed;
}
.tableSt01 thead tr th:last-child .inbox::after {
  display: none;
}
.innerTable.with-filter .tableSt01 thead th.file:before {
  content: "";
  position: absolute;
  top: 0;
  right: -30px;
  bottom: 0;
  left: 0;
  background-color: #f7f7f7;
  z-index: -1;
}
.tableSt01 thead th .str {
  font-size: 0;
  line-height: 0;
}
.innerTable.summary .tableSt01 thead th .str {
  min-height: 100%;
}
.tableSt01 thead th .str > strong {
  display: inline-block;
  font-size: 13px;
  font-weight: bold;
  line-height: 1.23;
  letter-spacing: -0.13px;
  color: #363636;
}
.tableSt01 thead th .str .sort {
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translateY(-50%);
  width: 16px;
  font-size: 0;
  line-height: 0;
}
.tableSt01 thead th .str button img {
  width: 100%;
}
.tableSt01 thead th .str .filter {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 28px;
  font-weight: normal;
  font-size: 0;
  line-height: 0;
}
.tableSt01 thead th .str .filter button {
  width: 16px;
  height: 16px;
  background: url(/images/filter.png) no-repeat center / cover;
}
.tableSt01 thead th .str .filter.active button {
  background-image: url(/images/filter-active.png);
}
.tableSt01 thead th .str .filter.active .dropdown-wrap {
  display: block;
  width: 160px;
  top: calc(100% + 24px);
  left: auto;
  right: -28px;
}
.tableSt01 thead th.center {
  text-align: center;
}
.tableSt01 thead th.center .inbox {
  padding: 0;
  justify-content: center;
  display: flex;
  align-items: center;
